import store from "./store";
import Vue from "vue";
import Router from "vue-router";
// import Home from "./views/Home.vue";

Vue.use(Router);

const routerOptions = [
  { path: "/", component: "Home" },
  { path: "/medical", page: true, component: "medical" },
  { path: "/household", page: true, component: "household" },
  { path: "/legal", page: true, component: "legal" },
  { path: "/entertainment", page: true, component: "entertainment" },
  { path: "/financial", page: true, component: "financial" },
  { path: "/signin", component: "Signin" },
  { path: "/signup", component: "Signup" },
  { path: "/contact", component: "Contact" },
  { path: "/landing", component: "Landing" }
];

const routes = routerOptions.map(route => {
  let component = null;
  if (route.page) {
    component = () => import(`@/components/pages/${route.component}.vue`);
  } else {
    component = () => import(`@/components/${route.component}.vue`);
  }
  return {
    ...route,
    component: component
  };
});

// export default new Router({
//   mode: "history",
//   base: process.env.BASE_URL,
//   routes
// });
let router = new Router({
  mode: "history",
  routes: routes
});

router.beforeEach((to, from, next) => {
  // redirect to login page if not logged in and trying to access a restricted page
  const publicPages = ["/", "/signin", "/signup"];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = store.getters.isAuthenticated;

  if (authRequired && !loggedIn) {
    return next("/signin");
  }

  next();
});

export default router;
