<template>
  <v-app>
    <v-toolbar
      class="folks_toolbar"
      dark
      color="#255F7C"
      app
      extended
      extension-height="7"
    >
      <v-layout row wrap text-xs-center pt2>
        <v-flex xs12 sm1>
          <router-link
            :to="mainLocation"
            tag="img"
            src="/img/logos/folk.ico"
            contain
            height="50"
            style="cursor: pointer"
          >
            <!-- <v-img
            contain
            height="50"
            aspect-ratio="1"
            src="/img/logos/folk.ico"
            ></v-img>-->
          </router-link>
        </v-flex>
        <v-flex xs12 sm7 folks_menu>
          <v-layout align-center text-xs-center v-bind="binding">
            <v-flex
              align-self-center
              v-for="item in menuItems"
              :key="item.title"
            >
              <v-btn flat class="text-xs-center px-3" :to="item.path">
                {{ item.title }}
              </v-btn>
            </v-flex>
          </v-layout>
        </v-flex>
        <v-flex text-xs-center align-self-center xs12 sm3>
          <v-text-field
            light
            solo
            class="px-3 folks_search"
            append-icon="search"
            placeholder="Search..."
          ></v-text-field>
        </v-flex>
        <v-flex v-if="isLoggedIn" align-self-center xs12 sm1>
          <v-btn @click="logout()" tag="span" flat small>Log out</v-btn>
        </v-flex>
        <v-flex v-else align-self-center xs12 sm1>
          <v-btn mr-1 to="/signin" tag="span" flat small>Sign In</v-btn>
          <v-btn to="/signup" tag="span" flat small>Sign Up</v-btn>
        </v-flex>
      </v-layout>

      <v-progress-linear
        class="pa-0 ma-0"
        slot="extension"
        color="#7DA5C6"
        v-model="valueDeterminate"
      ></v-progress-linear>
    </v-toolbar>

    <v-content>
      <router-view></router-view>
    </v-content>
    <v-footer dark class="pa-3">
      <v-layout row wrap>
        <v-flex xs6>
          <v-btn to="/contact" flat small>Contact us</v-btn>
        </v-flex>
        <v-flex xs6 class="pt-2">
          <div>
            <span>Copyright</span>
            &copy; {{ new Date().getFullYear() }}
          </div>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      valueDeterminate: 100,
      sidebar: false,
      menuItems: [
        { title: "Medical", path: "/medical" },
        { title: "Household", path: "/household" },
        { title: "Legal", path: "/legal" },
        { title: "Entertainment", path: "/entertainment" },
        { title: "Financial", path: "/financial" }
      ]
    };
  },
  computed: {
    appTitle() {
      return this.$store.state.appTitle;
    },
    isLoggedIn() {
      return this.$store.state.isAuthenticated;
    },
    mainLocation() {
      if (this.isLoggedIn) {
        return { path: "/landing" };
      } else {
        return { path: "/" };
      }
    },
    binding() {
      const binding = {};

      if (this.$vuetify.breakpoint.xsOnly) binding.column = true;

      return binding;
    }
  },
  methods: {
    logout() {
      this.$store
        .dispatch("logoutUser")
        .then(res => {
          console.log("logout:", res);
          this.$router.push({ path: "/signin" });
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
