import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    appTitle: "folks",
    id: "",
    isAuthenticated: false,
    bookmarks: {
      medical: [],
      household: [],
      legal: [],
      entertainment: [],
      financial: []
    },
    chips: [
      {
        info: "increased traffic",
        icon: "trending_up",
        color: "red",
        textColor: "white"
      },
      {
        info: "reduced traffic",
        icon: "trending_down",
        color: "green",
        textColor: "white"
      },
      {
        info: "trending",
        icon: "whatshot",
        color: "orange",
        textColor: "white"
      },
      {
        info: "recently updated",
        icon: "cached",
        color: "blue",
        textColor: "white"
      }
    ]
  },
  getters: {
    isAuthenticated: state => {
      return state.isAuthenticated;
    },
    getChips: state => {
      return state.chips;
    },
    getMedicalBookmarks: state => {
      return state.bookmarks.medical;
    },
    getHouseholdBookmarks: state => {
      return state.bookmarks.household;
    },
    getLegalBookmarks: state => {
      return state.bookmarks.legal;
    },
    getEntertainmentBookmarks: state => {
      return state.bookmarks.entertainment;
    },
    getFinancialBookmarks: state => {
      return state.bookmarks.financial;
    }
  },
  mutations: {
    setIsAuthenticated(state, auth) {
      state.isAuthenticated = auth;
    },
    logout(state, auth) {
      state.isAuthenticated = auth;
    },
    setID(state, id) {
      state.id = id;
    },
    setInitBookmarks(state, bookmarks) {
      state.bookmarks = bookmarks;
    },
    setMedicalBookmarks(state, bookmarks) {
      state.bookmarks.medical = bookmarks;
    },
    setHouseholdBookmarks(state, bookmarks) {
      state.bookmarks.household = bookmarks;
    },
    setLegalBookmarks(state, bookmarks) {
      state.bookmarks.legal = bookmarks;
    },
    setEntertainmentBookmarks(state, bookmarks) {
      state.bookmarks.entertainment = bookmarks;
    },
    setFinancialBookmarks(state, bookmarks) {
      state.bookmarks.financial = bookmarks;
    }
  },
  actions: {
    fetchUser({ commit }, details) {
      return new Promise((resolve, reject) => {
        // Try get the User from the LocalStorage
        if (localStorage.getItem(details.name + details.password)) {
          // bookmarks must come first
          commit(
            "setInitBookmarks",
            JSON.parse(localStorage.getItem(details.name + details.password))
          );
          commit("setID", details.name + details.password);
          commit("setIsAuthenticated", true);
          resolve(true);
        } else {
          reject(false);
        }
        // if successful resolve to true
        // if not the resolve to false for the display of error
      });
    },
    logoutUser({ commit }) {
      commit("logout", false);
    },
    createUser({ commit }, details) {
      return new Promise(resolve => {
        // Set the user ID and set authenticated

        commit("setID", details.name + details.password);
        commit("setIsAuthenticated", true);

        resolve(true);
      });
    },
    updateMedicalBookmarks({ commit }, bookmarks) {
      // this will take the bookmarks and update the states
      commit("setMedicalBookmarks", bookmarks);
    },
    updateHouseholdBookmarks({ commit }, bookmarks) {
      // this will take the bookmarks and update the states
      commit("setHouseholdBookmarks", bookmarks);
    },
    updateLegalBookmarks({ commit }, bookmarks) {
      // this will take the bookmarks and update the states
      commit("setLegalBookmarks", bookmarks);
    },
    updateEntertainmentBookmarks({ commit }, bookmarks) {
      // this will take the bookmarks and update the states
      commit("setEntertainmentBookmarks", bookmarks);
    },
    updateFinancialBookmarks({ commit }, bookmarks) {
      // this will take the bookmarks and update the states
      commit("setFinancialBookmarks", bookmarks);
    }
  }
});
